import { Testimonial } from '@codecademy/brand-components/src';
import { VisualTheme } from '@codecademy/gamut/src';
import { select } from '@storybook/addon-knobs';
import React from 'react';

import { decoratedStory } from '../Templating';

export default decoratedStory('Brand', Testimonial);

const short_quote =
  "Coding isn't rocket science, its just falsely intimidating.";
const long_quote =
  'If it werent for Codecademy, Id probably be in my old job. Codecademy enabled me to learn quick lessons between calls, on my lunch break, while multitasking. It was fun and easy and I went from 0 coding skills to a promotion within 6 months.';
const imageUrl = 'https://content.codecademy.com/courses/free/boba.svg';

const laceyTestimonial = {
  firstName: 'Lacey',
  lastName: 'Bathala',
  occupation: 'Jr. Back-End Developer',
  company: 'Microsoft',
};

export const testimonialSmallWithAvatar = () => (
  <Testimonial
    testimonial={{ ...laceyTestimonial, quote: short_quote, imageUrl }}
    size="small"
    theme={select(
      'theme',
      [VisualTheme.DarkMode, VisualTheme.LightMode],
      VisualTheme.DarkMode
    )}
  />
);;

testimonialSmallWithAvatar.story = {
  name: 'Testimonial in size small (with Avatar)',
};

export const testimonialSmallWithoutAvatar = () => (
  <Testimonial
    testimonial={{ ...laceyTestimonial, quote: short_quote }}
    size="small"
    theme={select(
      'theme',
      [VisualTheme.DarkMode, VisualTheme.LightMode],
      VisualTheme.DarkMode
    )}
  />
);;

testimonialSmallWithoutAvatar.story = {
  name: 'Testimonial in size small (without Avatar)',
};

